@charset "utf-8";

/* Login */

.sp-login-container .sp-form-row {
  @apply block;
}

.sp-login-container .sp-form-row:last-child {
  @apply mb-0;
}

.sp-login-container .sp-form-row > label {
  @apply w-auto pt-0 pr-0 text-left;
}

.sp-login-container .sp-form-row > label:empty {
  @apply p-0;
}

/* Header */

.pace .pace-progress {
  top: 3rem;
}

/* Navigation */

header ul {
  @apply p-0 list-none;
}

.sp-nav-item {
  @apply border-t border-primary-900 text-white;
}

a.sp-nav-item {
  @apply text-white;
}

.sp-nav-item:not(.sp-nav-icon-item) {
  @apply block px-5 py-3;
}

.sp-nav-item .fas.sp-chevron:before,
.sp-nav-sub-menu .fas.sp-chevron:before {
  content: "\f077";
}

.sp-nav-dropdown {
  @apply hidden bg-primary text-sm;
}

.sp-nav-dropdown li {
  @apply border-b border-secondary;
}

.sp-nav-dropdown li:last-child {
  @apply border-b-0;
}

.sp-nav-dropdown li > a {
  @apply block px-5 py-3 text-primary;
}

.sp-nav-sub-menu + .sp-nav-dropdown li > a,
.sp-nav-sub-menu + .sp-nav-dropdown li.sp-multi-list > .sp-title {
  @apply pl-10;
}

.sp-nav-item:not(.sp-nav-icon-item):hover,
.sp-nav-dropdown li > a:hover {
  @apply bg-primary-900 text-white cursor-pointer;
}

.sp-nav-dropdown li.sp-multi-list {
  @apply py-2;
}

.sp-nav-dropdown li.sp-multi-list .sp-title {
  @apply px-5 py-1 font-bold text-secondary uppercase;
}

.sp-nav-dropdown li.sp-multi-list > a {
  @apply block py-1 truncate;
}

@screen lg {
  nav {
    @apply block !important;
  }

  .sp-nav-item {
    @apply border-0;
  }

  .sp-nav-item:not(.sp-nav-icon-item) {
    @apply inline-block px-3 py-4;
  }

  .sp-nav-item .fas.sp-chevron:before {
    content: "\f078";
  }

  .sp-nav-sub-menu .fas.sp-chevron:before {
    content: "\f054";
  }

  .sp-nav-dropdown {
    @apply absolute top-100 min-w-4xl max-w-6xl rounded-b shadow;
  }

  .sp-theme-dark .sp-nav-dropdown {
    @apply border border-secondary shadow-md;
  }

  .sp-nav-item:hover + .sp-nav-dropdown,
  .sp-nav-item.sp-hover + .sp-nav-dropdown,
  .sp-nav-sub-menu:hover + .sp-nav-dropdown,
  .sp-nav-dropdown:hover {
    @apply block;
  }

  nav > ul > li:hover > .sp-nav-item,
  .sp-nav-item.sp-hover,
  nav > ul > li > ul > li:hover > .sp-nav-sub-menu {
    @apply bg-primary-900 text-white cursor-pointer;
  }

  .sp-nav-sub-menu + .sp-nav-dropdown {
    @apply top-0 left-100;
  }

  .sp-theme-dark .sp-nav-sub-menu + .sp-nav-dropdown {
    top: -1px;
  }

  .sp-nav-dropdown li {
    @apply border-b;
  }

  .sp-nav-dropdown li:not(.sp-multi-list):last-child > a:hover {
    @apply rounded-b;
  }

  .sp-nav-sub-menu + .sp-nav-dropdown li > a {
    @apply pl-5;
  }
}

@screen xl {
  .sp-nav-item:not(.sp-nav-icon-item) {
    @apply px-4;
  }
}

.sp-mobile-nav svg {
  @apply fill-current;
  height: 1.375rem;
  width: 1.375rem;
}

.sp-private-messages a:hover {
  @apply text-white;
}

/* Content */

#wrapper {
  height: calc(100% - 3rem);
}

@screen lg {
  #wrapper {
    @apply h-full;
  }
}

/* Sidebar */

#sidebar .sp-sidebox {
  @apply hidden border-b border-tertiary;
}

#sidebar .sp-sidebox .sp-collapsible {
  @apply h-auto w-full p-0 shadow-none rounded-none text-secondary cursor-pointer;
}

#sidebar .sp-sidebox .sp-collapsible:hover,
#sidebar .sp-sidebox .sp-collapsible:hover h3 {
  @apply text-primary;
}

#sidebar .sp-sidebox .sp-icon,
#sidebar .sp-sidebox .sp-collapsible .sp-chevron {
  @apply flex-none my-1/2;
}

#sidebar .sp-sidebox h3 {
  @apply flex-grow my-0 ml-3 text-xl font-bold leading-snug;
}

#sidebar .sp-sidebox .sp-collapsible + * {
  @apply mt-4;
}

#sidebar .sp-sidebox a {
  @apply text-primary;
}

#sidebar .sp-sidebox a:hover {
  @apply text-secondary;
}

#sidebar .sp-sidebox input,
#sidebar .sp-sidebox select,
#sidebar .sp-sidebox .selectize-input {
  @apply px-3/2;
}

#sidebar .sp-sidebox select {
  @apply pr-7;
}

#sidebar .sp-sidebox input[type="checkbox"],
#sidebar .sp-sidebox input[type="radio"] {
  @apply p-0;
}

#sidebar .sp-sidebox input,
#sidebar .sp-sidebox select,
#sidebar .sp-sidebox button:not(.sp-button-submit):not(.as-link),
#sidebar .sp-sidebox a.sp-button:not(.sp-button-submit),
#sidebar .sp-sidebox .redactor-in,
#sidebar .sp-sidebox .selectize-input,
#sidebar .sp-sidebox input[type="checkbox"]::before,
#sidebar .sp-sidebox input[type="radio"]::before,
#sidebar .sp-sidebox input[type="checkbox"]:hover::before,
#sidebar .sp-sidebox input[type="radio"]:hover::before {
  @apply bg-primary border-primary;
}

#sidebar .sp-sidebox input[type="radio"]:checked::before,
#sidebar .sp-sidebox input[type="radio"]:checked:hover::before {
  @apply bg-green-600;
}

#sidebar .sp-sidebox .selectize-dropdown {
  @apply bg-secondary border-2 border-t-0 border-primary;
}

#sidebar .sp-sidebox button:not(.sp-button-submit):not(.as-link):not([disabled]):hover,
#sidebar .sp-sidebox a.sp-button:not(.sp-button-submit):hover,
#sidebar .sp-sidebox .selectize-dropdown .active {
  @apply bg-tertiary;
}

#sidebar .sp-sidebox hr {
  @apply my-4;
}

#sidebar .sp-toggle-sidebar {
  @apply bg-primary;
}

.sp-theme-dark #sidebar .sp-toggle-sidebar {
  @apply bg-tertiary;
}

#sidebar .fas.sp-chevron:before {
  content: "\f101";
}

#sidebar.sp-sidebar-open .fas.sp-chevron:before {
  content: "\f100";
}

#sidebar.sp-sidebar-open {
  @apply fixed z-10001 shadow;
  height: calc(100% - 3rem);
}

#sidebar.sp-sidebar-open .sp-sidebox {
  @apply block p-6;
}

#sidebar.sp-sidebar-open .sp-sidebox.sp-hidden {
  @apply hidden;
}

@screen lg {
  #sidebar:not(.sp-sidebar-closed) .sp-sidebox {
    @apply block p-6;
  }

  #sidebar:not(.sp-sidebar-closed) .sp-sidebox.sp-hidden {
    @apply hidden;
  }

  #sidebar:not(.sp-sidebar-closed) .fas.sp-chevron:before {
    content: "\f100";
  }

  #sidebar.sp-sidebar-closed .fas.sp-chevron:before {
    content: "\f101";
  }
}

#sidebar .sp-sidebox ul {
  @apply pl-0 list-none;
}

#sidebar .sp-sidebox li {
  @apply pb-3/2;
}

#sidebar .sp-sidebox li:last-child {
  @apply pb-0;
}

#sidebar .sp-sidebox li.sp-active {
  @apply font-bold;
}

#sidebar .sp-form-row {
  @apply flex-wrap mb-4;
}

#sidebar .sp-form-row label {
  @apply w-full pt-0 pr-0 text-left;
}

#sidebar .sp-form-row .sp-input-container {
  @apply ml-0;
}

#sidebar .sp-form-row input[type="email"],
#sidebar .sp-form-row input[type="number"],
#sidebar .sp-form-row input[type="password"],
#sidebar .sp-form-row input[type="search"],
#sidebar .sp-form-row input[type="tel"],
#sidebar .sp-form-row input[type="text"],
#sidebar .sp-form-row input[type="url"] {
  @apply w-full;
}

#sidebar .sp-form-row select {
  @apply max-w-full;
}

#sidebar ul.sp-tabs {
  @apply mx-0 mb-4 border border-tertiary rounded;
}

#sidebar ul.sp-tabs li {
  @apply p-0 mr-0;
}

#sidebar ul.sp-tabs li a {
  @apply bg-transparent py-1 text-secondary;
}

#sidebar ul.sp-tabs li.sp-active a {
  @apply bg-tertiary rounded-none;
}

#sidebar ul.sp-tabs li.sp-active a,
#sidebar ul.sp-tabs li a:hover {
  @apply text-primary;
}

/* Main */

#sidebar.sp-sidebar-open + #content {
  @apply ml-6;
}

@screen sm {
  #sidebar.sp-sidebar-open + #content {
    @apply ml-8;
  }
}

#content > div > .sp-alert-sticky {
  @apply sticky top-0 mb-0 rounded-none z-50;
}

#content > div > .sp-quick-actions ~ .sp-alert-sticky {
  top: 3rem;
}

h1 {
  @apply mb-6 text-2xl;
}

@screen sm {
  h1 {
    @apply text-3xl;
  }
}

h2 {
  @apply my-6 text-secondary text-xl;
}

h3 {
  @apply my-6 text-secondary text-lg;
}

h4 {
  @apply my-4 text-secondary;
}

.sp-quick-actions {
  @apply sticky top-0 px-3 py-1 bg-primary shadow z-100;
}

.sp-theme-dark .sp-quick-actions {
  @apply shadow-md;
}

.sp-quick-actions ul {
  @apply p-0;
}

.sp-quick-actions ul li.sp-action-group {
  @apply pr-1/2;
}

.sp-quick-actions ul li.sp-action-group:not(.sp-hidden) + li.sp-action-group {
  @apply pl-1/2 border-l border-secondary;
}

.sp-quick-actions .sp-action {
  @apply px-3 bg-transparent shadow-none;
}

.sp-quick-actions .sp-action:not([disabled]):hover {
  @apply bg-tertiary shadow-inner text-primary;
}

.sp-quick-actions .sp-reply-type .sp-action.sp-active[data-type="0"] {
  @apply text-blue-600;
}

.sp-quick-actions .sp-reply-type .sp-action.sp-active[data-type="1"] {
  @apply text-orange-600;
}

.sp-quick-actions .sp-reply-type .sp-action.sp-active[data-type="2"] {
  @apply text-purple-600;
}

@screen xs {
  .sp-quick-actions ul li.sp-action-group {
    @apply pr-1;
  }

  .sp-quick-actions ul li.sp-action-group + li.sp-action-group {
    @apply pl-1;
  }

  .sp-quick-actions .sp-action {
    @apply px-4;
  }
}

@screen print {
  .sp-quick-actions {
    @apply hidden;
  }
}

.sp-title-description,
.sp-subtitle-description,
.breadcrumb {
  @apply -mt-4 mb-6 text-secondary;
}

.breadcrumb a {
    @apply text-secondary;
}

.breadcrumb a:hover {
    @apply text-primary;
}

.breadcrumb .fas {
    @apply text-xs;
}

/* Header search */

#header .search-form .selectize-control:before {
  @apply absolute text-white font-icon font-bold z-10;
  content: "\f002";
  left: 0.5rem;
  top: 1rem;
}

#header .search-form input[type="search"],
#header .search-form .selectize-input {
  @apply h-12 pl-8 bg-primary-800 border-0 rounded-none;
  line-height: 2.25rem;
}

#header .search-form input[type="search"]:focus,
#header .search-form .selectize-input.focus {
  @apply w-full bg-primary-800;
}

#header .search-form input {
  @apply text-white;
}

#header .search-form input::placeholder {
  @apply text-primary-200 opacity-100;
}

@screen lg {
  #header .search-form div.selectize-control:before {
    @apply cursor-pointer;
  }

  #header .search-form input[type="search"],
  #header .search-form .selectize-input {
    @apply w-8 pl-4 cursor-pointer;
  }

  #header .search-form input[type="search"]:focus,
  #header .search-form .selectize-input.focus {
    @apply pl-8 bg-primary-600 cursor-auto;
    min-width: 24rem;
  }

  #header .search-form input[type="search"]::placeholder,
  #header .search-form .selectize-input input::placeholder {
    @apply opacity-0;
  }

  #header .search-form input[type="search"]:focus::placeholder,
  #header .search-form .selectize-input.focus input::placeholder {
    @apply opacity-100;
  }
}

#header .search-form .selectize-control.loading:before {
  background: 0;
}

#header .search-form .selectize-control.loading:after {
  @apply block absolute text-white font-icon font-bold z-50;
  animation: fa-spin 2s infinite linear;
  content: "\f110";
  height: 1rem;
  right: 0.75rem;
  top: 1rem;
  width: 1rem;
}

#header .search-form .selectize-dropdown [data-selectable] {
  @apply p-0;
}

#header .search-form .selectize-dropdown [data-selectable] .highlight {
  @apply bg-transparent rounded-none font-bold;
}

#header .search-form .selectize-dropdown a {
  @apply text-primary;
}

#header .search-form .selectize-dropdown .selectize-dropdown-content {
  @apply max-h-full;
}

/* Widgets */

.sp-widget {
  @apply mb-6 border border-tertiary rounded;
}

.sp-widget:last-child {
  @apply mb-0;
}

.sp-widget .sp-widget-header {
  @apply px-3 py-2 bg-secondary rounded-t text-xl font-bold cursor-pointer;
}

.sp-widget.sp-widget-minimised .sp-widget-header {
  @apply rounded;
}

.sp-widget .sp-widget-header .sp-widget-toggle {
  @apply px-1 py-2 text-sm cursor-pointer float-right;
}

.sp-widget .sp-widget-content {
  @apply p-3;
}

/* Ticket Grid */

.sp-ticket-metadata {
  @apply text-secondary;
}

.sp-ticket-metadata .fas {
  @apply text-xs;
}

.sp-ticket-metadata .sp-messages-count {
  @apply bg-gray-400;
}

.sp-theme-dark .sp-ticket-metadata .sp-messages-count {
  @apply bg-gray-600;
}

.sp-ticket-metadata .sp-notes-count {
  @apply bg-yellow-400;
}

.sp-theme-dark .sp-ticket-metadata .sp-notes-count {
  @apply bg-yellow-700;
}

.sp-ticket-metadata .sp-messages-count,
.sp-ticket-metadata .sp-notes-count {
  @apply text-primary;
}

.sp-theme-dark .sp-ticket-metadata .sp-messages-count,
.sp-theme-dark .sp-ticket-metadata .sp-notes-count {
  @apply text-white;
}

/* SLA Plans */

.sp-duration {
  @apply inline-block;
}

.sp-duration, .sp-duration input {
  @apply text-center;
}

.sp-duration .sp-description {
  @apply mt-1 text-xs;
}

.sp-condition-group {
  @apply border border-tertiary rounded;
}

.sp-condition-group .sp-condition-group-header {
  @apply px-3 py-2 mb-0 bg-secondary rounded-t;
}

.sp-condition-group .sp-condition-group-header h3 {
  @apply m-0;
}

.sp-condition-group .sp-condition-group-inner {
  @apply pb-3 overflow-auto;
}

.sp-condition-group table td {
  @apply p-3 pb-0 border-0;
}

.sp-condition-group + .sp-form-row {
  @apply pt-1;
}

.sp-condition-group .sp-condition-group-inner .sp-condition-group-buttons {
  @apply p-3 pb-0;
}

.sp-condition-group .sp-collapsible {
  @apply rounded-b-none shadow-none;
}

.sp-condition-group .sp-collapsible + .sp-hidden {
  @apply mt-0;
}

/* Feedback */

.field-option-container > .option,
.field .add-option {
  @apply mt-2;
}

.field-option-container > .option .sp-translatable-modal {
  left: 1.75rem;
}

/* Custom Fields */

.sp-customfield-options .sp-form-row.option {
  @apply flex pb-0 items-center;
}

.sp-customfield-options .sp-form-row.option.sp-hidden {
  @apply hidden;
}

.sp-customfield-options .sp-form-row.option label {
  @apply mb-0 mr-2;
}

@screen md {
  .sp-customfield-options .sp-form-row.option label {
    @apply mr-0;
  }

  .sp-customfield-options .ui-state-highlight {
    @apply ml-48;
  }
}

/* Departments/Actions */

.group.sortable-drag td,
.rule.sortable-drag td{
  @apply invisible border-b-0;
}

.group.sortable-drag td.drag-visible,
.rule.sortable-drag td.drag-visible {
  @apply visible;
}

/* Pagination */

ul.pagination {
  @apply pl-0 my-6 list-none text-center;
}

ul.pagination li {
  @apply inline;
}

ul.pagination li > a,
ul.pagination li > span {
  @apply inline-block px-3 py-3/2 border border-tertiary rounded;
}

ul.pagination li > a:hover,
ul.pagination li.active > span {
  @apply bg-secondary;
}

ul.pagination li.disabled > span {
  @apply opacity-50;
}

/* Translatable input */

.sp-input-translatable {
  @apply relative;
}

.sp-form-full .sp-input-translatable div.sp-input-translatable-container,
.sp-input-translatable input {
  @apply w-full;
}

.sp-input-translatable div.sp-input-translatable-container > input {
  @apply pr-10;
}

.sp-input-translatable.sp-input-translatable-small input,
.sp-input-translatable.sp-input-translatable-small .sp-translatable-modal {
  @apply max-w-5xl;
}

.sp-input-translatable .fa-language {
  @apply absolute p-1 mt-2 text-secondary cursor-pointer;
  right: 0.5rem;
}

.sp-theme-dark .sp-translatable-modal {
  @apply border border-secondary shadow-md;
}

/* Ticket Grid */

table#ticketGridTable tbody tr td {
  @apply align-top;
}

table.dataTable tbody tr.sp-ticket-viewed td {
  @apply bg-secondary opacity-75;
}

table.dataTable tbody tr.sp-ticket-viewed.selected td {
  @apply bg-primary-200 opacity-100;
}

.sp-theme-dark table.dataTable tbody tr.sp-ticket-viewed.selected td {
  @apply bg-primary-800;
}

/* Ticket View */

.sp-quick-actions + .ticket-viewing {
  top: 3rem;
}

@screen md {
  .ticket-viewing {
    @apply sticky top-0 z-50;
  }
}

.sp-ticket-subject.sp-can-edit-subject {
  @apply inline-block border border-transparent;
}

.sp-ticket-subject.sp-can-edit-subject:hover {
  @apply relative pl-3 pr-10 border-tertiary rounded cursor-pointer;
}

.sp-ticket-subject.sp-can-edit-subject:hover:before {
  @apply absolute inline-block right-0 p-3 font-icon font-bold text-sm;
  content: "\f044";
}

.sp-message {
  @apply bg-primary;
}

.sp-message.sp-note {
  @apply bg-orange-100 border-orange-300;
}

.sp-theme-dark .sp-message.sp-note {
  @apply bg-orange-900 border-0;
}

.sp-message.sp-forward {
  @apply bg-purple-100 border-purple-200;
}

.sp-theme-dark .sp-message.sp-forward {
  @apply bg-purple-900 border-0;
}

.sp-message .sp-message-header .fas {
  @apply text-secondary;
}

.sp-message .sp-message-header .fas:hover {
  @apply text-primary;
}

.sp-message.sp-message-collapsed {
  @apply opacity-50 text-secondary;
}

.sp-message.sp-message-collapsed,
.sp-message.sp-message-collapsible .sp-message-header {
  @apply cursor-pointer;
}

.sp-message.sp-message-collapsed .sp-message-text {
  @apply block;
}

.sp-message.sp-message-collapsed .sp-message-text-edit,
.sp-message.sp-message-collapsed .sp-attachments {
  @apply sp-hidden;
}

.sp-message div.expandable:before {
  @apply inline-block h-4 px-1 my-3 bg-secondary border border-tertiary rounded;
  content: '...';
  line-height: 0.375rem;
}

.sp-message div.expandable:hover:before {
  @apply bg-tertiary cursor-pointer;
}

.sp-message div.expandable + div.supportpal_quote {
  @apply hidden text-tertiary;
}

div.sp-collapsed-messages {
  @apply p-1 my-1 text-secondary text-center cursor-pointer;
  background: linear-gradient(to bottom, var(--color-background-primary), var(--color-background-primary) 90%, var(--color-background-secondary) 10%, var(--color-background-secondary));
  background-size: 100% 11px;
}

div.sp-collapsed-messages span {
  @apply px-3 py-1 bg-primary;
}

div.sp-collapsed-messages + .sp-message {
  @apply mt-0;
}

.recipients {
  @apply border border-b-0 border-tertiary rounded-t;
}

.recipients div.sp-table-row > div.sp-table-cell {
  @apply block w-full px-2 pb-2 align-middle;
}

.recipients div.sp-table-row:first-child > div.sp-table-cell:first-child {
  @apply pt-2;
}

.recipients div.sp-table-row > div.sp-table-cell:first-child {
  @apply w-auto pb-1 text-secondary whitespace-no-wrap;
}

.recipients .selectize-input {
  @apply min-w-0 whitespace-normal;
}

.recipients ul.sp-recipients-list {
  @apply inline p-0 list-none;
}

.recipients ul.sp-recipients-list > li {
  @apply inline;
}

.recipients ul.sp-recipients-list > li.sp-hidden {
  @apply hidden;
}

.recipients ul.sp-recipients-list > li:not(.sp-hidden) ~ li::before {
  content: ", ";
}

.forward-form .recipients .add-cc, .forward-form .recipients .add-bcc {
  @apply relative z-10;
}

@screen sm {
  .recipients div.sp-table-row > div.sp-table-cell {
    @apply table-cell px-1 pb-1;
  }

  .recipients div.sp-table-row:first-child > div.sp-table-cell {
    @apply pt-1;
  }

  .recipients div.sp-table-row > div.sp-table-cell:first-child {
    @apply pl-2 text-right;
  }
}

.reply-form .sp-input-error {
  @apply block px-3 py-1 bg-red-600 text-white;
}

.sp-draft-message hr {
  @apply border-orange-500;
}

.sp-theme-dark .sp-draft-message hr {
  @apply border-orange-900;
}

.sp-draft-message img:not(.sp-avatar) {
  @apply max-w-full;
}

.sp-reply-options {
  @apply w-full border border-tertiary rounded;
}

.sp-reply-options-content {
  @apply p-3;
}

.sp-reply-option {
  @apply mb-2;
}

.sp-reply-option:last-child {
  @apply mb-0;
}

@screen lg {
  .sp-reply-options {
    @apply w-72;
  }
}

/* Sidebar - ticket details */

ul.sp-ticket-details {
  @apply list-none;
}

ul.sp-ticket-details > li {
  @apply mb-3/2;
}

ul.sp-ticket-details > li:last-child {
  @apply mb-0;
}

ul.sp-ticket-details li .sp-title {
  @apply text-secondary text-sm;
}

ul.sp-ticket-details li .sp-detail {
  @apply font-bold;
}

ul.sp-ticket-details li .sp-detail a.sp-detail-link {
  @apply border-b border-primary-800 border-dotted;
}

ul.sp-ticket-details li .sp-detail a.sp-detail-link:hover {
  @apply border-solid;
}

ul.sp-ticket-details li .sp-detail > select,
ul.sp-ticket-details li .sp-detail > .selectize-control {
  @apply w-full max-w-full sp-mt-1;
}

ul.sp-ticket-details li .sp-detail .create-new-user {
  @apply font-normal;
}

ul.sp-ticket-details li .sp-detail .create-new-user a.new-user-toggle {
  @apply border-0;
}

ul.sp-ticket-details li .sp-detail .new-user-form input,
ul.sp-ticket-details li .sp-detail .new-user-form .selectize-control {
  @apply w-full mb-2;
}

ul.sp-ticket-details li .sp-detail .selectize-control {
  @apply font-normal;
}

/* Code Editor */

.merge-field_container {
  @apply relative;
}

.merge-field_preview {
  @apply absolute top-0 w-full p-3 bg-secondary border border-tertiary rounded overflow-auto z-10001;
}

.merge-field_preview.loadinggif:before {
  @apply absolute -mt-12 -ml-8 font-icon font-bold text-primary-600 text-6xl;
  animation: fa-spin 2s infinite linear;
  content: "\f013";
  top: 50%;
  left: 50%;
}

.sp-theme-dark .merge-field_preview.loadinggif:before {
  @apply text-primary-200;
}

/* Comments */

.sp-comment {
  @apply p-4;
}

.sp-comment .sp-comment {
  @apply border-0 p-0;
}

.sp-comment .sp-comment:first-child {
  @apply mt-3;
}
